<template>
  <div id="login">
      <div class="login-form">
          <div class="appfont">
              Ëé´ÂÖã
          </div>
        <van-tabs v-model="formaActive" @click="form.username='';form.password='';">
            <van-tab title="ÁôªÈôÜ"></van-tab>
            <van-tab title="Ê≥®ÂÜå"></van-tab>
        </van-tabs>
        <van-form @submit="goHome">
            <template v-if="formaActive==0">
                <van-field
                    v-model.trim="form.username"
                    label="Ë¥¶Âè∑Ôºö"
                    placeholder="ËØ∑ËæìÂÖ•"
                />
                <van-field
                    v-model.trim="form.password"
                    type="password"
                    label="ÂØÜÁ†ÅÔºö"
                    placeholder="ËØ∑ËæìÂÖ•"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">ÁôªÈôÜ</van-button>
                </div>
            </template>
            <template v-else>
                <van-field
                    v-model.trim="form.username"
                    label="Ë¥¶Âè∑Ôºö"
                    placeholder="ËØ∑ËæìÂÖ•"
                />
                <van-field
                    v-model.trim="form.password"
                    type="password"
                    label="ÂØÜÁ†ÅÔºö"
                    placeholder="ËØ∑ËæìÂÖ•"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">Ê≥®ÂÜå</van-button>
                </div>
            </template>
        </van-form>
      </div>
  </div>
</template>

<script>
import { Notify } from 'vant';
export default {
    components: {
        [Notify.Component.name]: Notify.Component,
    },
    data(){
        return {
            formaActive: 0,
            form: {
                username:'',
                password: ''
            }
        }
    },
    methods: {
        goHome() {
            let _this = this;
            if(this.form.username&&this.form.password) {
                if(this.form.username.length<2||this.form.password.length<2) {
                    Notify('Ë¥¶Âè∑/ÂØÜÁ†Å‰∏çÂèØ‰ª•Â∞ë‰∫é2‰Ωç');
                    return;
                }
            } else {
                Notify('ËØ∑ËæìÂÖ•Ë¥¶Âè∑/ÂØÜÁ†Å');
                return;
            };
            if(this.formaActive==0) {
                let userList = localStorage.getItem('userList')?JSON.parse(localStorage.getItem('userList')):[];
                let user = {};
                userList.forEach((item)=>{
                    if(item.username==_this.form.username&&item.password==_this.form.password) {
                        user = item;
                    }
                },)
                setTimeout(() => {
                    if(user.username&&user.password) {
                        localStorage.setItem('userItem',JSON.stringify({
                            username: _this.form.username, 
                            password: _this.form.password
                        }))
                        _this.$router.push('/home')
                    } else {
                        Notify('ËØ•Áî®Êà∑Êú™Ê≥®ÂÜå,ËØ∑ÂâçÂæÄÊ≥®ÂÜå!');
                    }
                }, 300);
            } else {
                let userList = localStorage.getItem('userList')?JSON.parse(localStorage.getItem('userList')):[];
                let user = {};
                userList.forEach((item)=>{
                    if(item.username==_this.form.username&&item.password==_this.form.password) {
                        user = item;
                    }
                },)
                setTimeout(() => {
                    if(user.username&&user.password) {
                        Notify('ÊÇ®Â∑≤Ê≥®ÂÜå,ËØ∑ÂâçÂæÄÁôªÈôÜ!');
                    } else {
                        userList.push({
                           username: _this.form.username, 
                           password: _this.form.password 
                        })
                        Notify({ type: 'primary', message: 'Ê≥®ÂÜåÊàêÂäü,ËØ∑ÂâçÂæÄÁôªÈôÜ!' });
                        localStorage.setItem('userList',JSON.stringify(userList))
                    }
                }, 300);
            }
        }
    }
}
</script>

<style>

</style>


